<template>
  <div class="homepage-hero-module">
    <div class="video-container">
      <div :style="fixStyle" class="filter">
        <div id="home">
          <img style="width: 20%" src="http://114.116.238.138:9090/file/background/LOGO_2.png"/>
          <div id="login">
            <el-form class="login_form" :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
              <el-form-item label style="margin-bottom: 35px">
                <p style="font-size: 20px;margin-bottom: 10px;">管理员账号</p>
                <el-input style="width: 300px;font-size: 14px;" v-model="formLabelAlign.adminName"></el-input>
              </el-form-item>
              <el-form-item label style="margin-bottom: 15px">
                <p style="font-size: 20px;margin-bottom: 10px;">密码</p>
                <el-input style="width: 300px;font-size: 14px;" v-model="formLabelAlign.adminPassword"
                          show-password></el-input>
              </el-form-item>
            </el-form>
            <el-button class="login_btn" type="success" @click="submitForm()" round>登录</el-button>
          </div>
        </div>

      </div>
      <video :style="fixStyle" autoplay loop class="fillWidth" v-on:canplay="canplay">
        <source src="http://114.116.238.138:9090/file/background/Workaholic.mp4" type="video/mp4"/>
        浏览器不支持 video 标签，建议升级浏览器。
        <!--<source src="http://114.116.238.138:9090/file/background/Workaholic.webm" type="video/webm"/>-->
        <!--浏览器不支持 video 标签，建议升级浏览器。-->
      </video>
      <div class="poster hidden" v-if="!vedioCanPlay">
        <img :style="fixStyle" src="http://114.116.238.138:9090/file/background/Workaholic.jpg" alt="">
      </div>
    </div>
  </div>



</template>

<script>
  // var url = "http://localhost:8080";

  var url = "http://114.116.238.138:9090";
  export default {
    name: 'Home',
    data() {
      return {
        vedioCanPlay: false,
        fixStyle: '',
        labelPosition: 'top',
        formLabelAlign: {
          adminName: "",
          adminPassword: ""
        }
      };
    },
    mounted(){
      window.onresize = () => {
        const windowWidth = document.body.clientWidth
        const windowHeight = document.body.clientHeight
        const windowAspectRatio = windowHeight / windowWidth
        let videoWidth
        let videoHeight
        if (windowAspectRatio < 0.5625) {
          videoWidth = windowWidth
          videoHeight = videoWidth * 0.5625
          this.fixStyle = {
            height: windowWidth * 0.5625 + 'px',
            width: windowWidth + 'px',
            'margin-bottom': (windowHeight - videoHeight) / 2 + 'px',
            'margin-left': 'initial'
          }
        } else {
          videoHeight = windowHeight
          videoWidth = videoHeight / 0.5625
          this.fixStyle = {
            height: windowHeight + 'px',
            width: windowHeight / 0.5625 + 'px',
            'margin-left': (windowWidth - videoWidth) / 2 + 'px',
            'margin-bottom': 'initial'
          }
        }
      }
      window.onresize()
    },
    methods: {
      submitForm() {
        if (this.formLabelAlign.adminName == "" || this.formLabelAlign.adminName == null) {
          this.$alert('管理员账号不能空着哦！', '注意！', {
            confirmButtonText: '确定',
          });
        } else if (this.formLabelAlign.adminPassword == "" || this.formLabelAlign.adminPassword == null) {
          this.$alert('管理员密码不能空着哦！', '注意！', {
            confirmButtonText: '确定',
          });
        } else {
          let params = this.$qs.stringify({
            adminName: this.formLabelAlign.adminName,
            adminPassword: this.formLabelAlign.adminPassword
          });
          this.$axios
            .post(url + "/admin/login", params)
            .then(res => {
              if (res.data.status == 200) {
                this.$store.dispatch("login", res.data.data.adminId)
                this.$router.push({
                  name: 'index'
                })
              } else {
                this.$message({
                  type: 'error',
                  message: '账号或密码错误'
                });
              }
            })
            .catch(function () {
              this.$message({
                type: 'error',
                message: '登录异常'
              });
            });
        }
      },
      canplay() {
        this.vedioCanPlay = true
      }
    }
  }
</script>

<style>
  .homepage-hero-module,
  .video-container {
    position: relative;
    height: 100vh;
    overflow: hidden;
  }

  .video-container .poster img,
  .video-container video {
    z-index: 0;
    position: absolute;
  }

  .video-container .filter {
    z-index: 1;
    position: absolute;
    background: rgba(0, 0, 0, 0.4);
  }
  #home {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
  }

  #login {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font: 20px/1.4 "Helvetica Neue", "HelveticaNeue", Helvetica, Arial, sans-serif;

    background: rgba(255, 255, 255, 0.3);
    border-radius: 6px;
    width: 450px;
    height: 400px;
    padding: 0px !important;
    margin: 0px 0px 0px 0px !important;
    background-position: center 50%;
  }

  .login_form {
    display: flex;
    flex-direction: column;
    color: white;
  }

  .login_btn {
    font-size: 14px;
    background-color: red;
    height: 40px;
    width: 250px;
    margin-top: 30px;
    border-radius: 30px;
  }
</style>



